<template>
    <div id="accuracychart">
      <p v-show="false">{{accuracydata}}</p>
    </div>
</template>

<script>
  import * as d3 from 'd3'

  export default {
    name: 'AccuracyChart',
    data () {
      return {
        temp_data: [
          [{'x': 1, 'y': 0.04},{'x': 2, 'y': 0.66},{'x': 3, 'y': 0.57},{'x': 4, 'y': 0.54},{'x': 5, 'y': 0.48},{'x': 6, 'y': 0.23},{'x': 7, 'y': 0.28},{'x': 8, 'y': 0.05},{'x': 9, 'y': 0.44}],
          [{'x': 1, 'y': 0.13},{'x': 2, 'y': 0.55},{'x': 3, 'y': 0.8},{'x': 4, 'y': 0.33},{'x': 5, 'y': 0.6},{'x': 6, 'y': 0.76},{'x': 7, 'y': 0.1},{'x': 8, 'y': 0.95},{'x': 9, 'y': 0.94}],
          [{'x': 1, 'y': 0.36},{'x': 2, 'y': 0.72},{'x': 3, 'y': 0.79},{'x': 4, 'y': 0.75},{'x': 5, 'y': 0.7},{'x': 6, 'y': 0.63},{'x': 7, 'y': 0.9},{'x': 8, 'y': 0.5},{'x': 9, 'y': 1.0}]
        ],
        point_sum: 0
      }
    },
    props: {
      accuracydata: {
        type: Array,
        required: true
      },
      max_height: {
        type: Number,
        default: 2,
        required: true
      },
      min_height: {
        type: Number,
        default: 0,
        required: true
      },
      x_axis: {
        type: Number,
        default: 10,
        required: true
      }
    },
    mounted: function () {
      var winWidth = 0
      if (window.innerWidth) {
        winWidth = window.innerWidth
      } else if ((document.body) && (document.body.clientWidth)) {
        winWidth = document.body.clientWidth
      }
      if (document.documentElement && document.documentElement.clientWidth) {
        winWidth = document.documentElement.clientWidth
      }
      var width = winWidth * 0.25
      var height = 0.7 * width
      // console.log(width + 'd' + height)
      var margin = {top: height * 0.12, right: width * 0.12, bottom: height * 0.3, left: width * 0.12}
      var x = d3.scaleLinear().domain([1, this.x_axis]).range([0, width])
      // console.log(width + ' fenge ' + x(0.7))
      var y = d3.scaleLinear().domain([0, 5]).range([height, 0]) // height, 0y
      // x
      var xAxis = d3.axisBottom(x)
              .ticks(10)// 
              .tickSize(-height)//  size     
              .tickPadding(20) // 0,1,2x

      // y
      var yAxis = d3.axisLeft(y)
              .tickPadding(10)
              .tickSize(-width)

      var svg = d3.select('#accuracychart').append('svg')  // 
              .attr('width', width + margin.left + margin.right)
              .attr('height', height + margin.top + margin.bottom)
              .append('g')
              .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')

      svg.append('g') // x
        .attr('class', 'x axis')
        .attr('transform', 'translate(0,' + height + ')')
        .call(xAxis)

      svg.append('g') // y
        .attr('class', 'y axis')
        .call(yAxis)

      svg.append('g')
        .attr('class', 'y axis')
        .append('text')
        .attr('class', 'axis-label')
        .attr('transform', 'rotate(-90)') // 3m
        .attr('y', (-margin.left) + 14) // 
        .attr('x', -height / 2)
        .attr('font-size', 16)
        .text('Metrics')

      svg.append('g')
        .attr('class', 'x axis')
        .append('text')
        .attr('y', height + margin.top + 12)
        .attr('x', width / 2)
        .attr('font-size', 16)
        .text('Epoch')

      svg.append('clipPath')
        .attr('id', 'clip')
        .append('rect')
        .attr('width', width)
        .attr('height', height)

      var line = d3.line()
        .curve(d3.curveLinear)
        .x(function (d) { return x(d.x) })
        .y(function (d) { return y(d.y) })

      var linebase = svg.selectAll('.line')

      var line_color = ['#FF9933', '#0099FF', '#FF3300']

      linebase.data(this.accuracydata)
        .enter()
        .append('path')
        .attr('class', 'line')
        .attr('clip-path', 'url(#clip')
        .attr('stroke', function (d, i) {
          return line_color[i % 3]
        })
        .style('stroke-width', '1.5px')
        .attr('d', line)

      var points = svg.selectAll('.dots')
        .data(this.accuracydata)
        .enter()
        .append('g')
        .attr('class', 'dots')
        .attr('clip-path', 'url(#clip)')

      points.selectAll('.dot')
        .data(function (d, index) {
          var a = []
          d.forEach(function (point, i) {
            a.push({'index': index, 'point': point})
          })
          return a
        })
        .enter()
        .append('circle')
        .attr('class', 'dot')
        .attr('r', 2)
        .attr('fill', function (d , i) {
          return line_color[d.index % 3]
        })
        .attr('transform', function (d) {
          return 'translate(' + x(d.point.x) + ',' + y(d.point.y) + ')'
        })

      /*

      svg.append('rect')
        .attr('fill', 'white')
        .attr('height', 50)
        .attr('width', 100)
        .attr('stroke', 'blue')
        .attr('transform', 'translate(0,' + (margin.top + 20) + ')')


      svg.append('rect')
        .attr('fill', 'blue')
        .attr('height', 10)
        .attr('width', 20)
        .attr('transform', 'translate(0,' + (height + margin.top + 20 + 25) + ')')
      */
    },
    updated: function () {
      var body = d3.select('#accuracychart')
      d3.select('body').selectAll('.d3-tip-accuracy').remove()
      d3.select('body').selectAll('#tip').remove()

      this.point_sum = this.accuracydata[0].length
      var winWidth = 0
      if (window.innerWidth) {
        winWidth = window.innerWidth
      } else if ((document.body) && (document.body.clientWidth)) {
        winWidth = document.body.clientWidth
      }
      if (document.documentElement && document.documentElement.clientWidth) {
        winWidth = document.documentElement.clientWidth
      }
      var width = winWidth * 0.25
      var height = 0.7 * width
      // console.log(width + 'd' + height)
      var margin = {top: height * 0.12, right: width * 0.12, bottom: height * 0.3, left: width * 0.12}
      var x = d3.scaleLinear().domain([1, this.x_axis]).range([0, width])
      // console.log(width + ' fenge ' + x(0.7))
      var y = d3.scaleLinear().domain([this.min_height, this.max_height]).range([height, 0]) // height, 0y
      // x
      var xAxis = d3.axisBottom(x)
              .ticks(10)// 
              .tickSize(-height)//  size     
              .tickPadding(20) // 0,1,2x

      // y
      var yAxis = d3.axisLeft(y)
              .tickPadding(10)
              .tickSize(-width)

      d3.select('svg').remove()

      var svg = d3.select('#accuracychart').append('svg')  // 
              .attr('width', width + margin.left + margin.right)
              .attr('height', height + margin.top + margin.bottom)
              .append('g')
              .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')

      svg.append('g') // x
        .attr('class', 'x axis')
        .attr('transform', 'translate(0,' + height + ')')
        .call(xAxis)

      svg.append('g') // y
        .attr('class', 'y axis')
        .call(yAxis)

      svg.append('g')
        .attr('class', 'y axis')
        .append('text')
        .attr('class', 'axis-label')
        .attr('transform', 'rotate(-90)') // 3m
        .attr('y', (-margin.left) + 14) // 
        .attr('x', -height / 2)
        .attr('font-size', 16)
        .text('Metrics')

      svg.append('g')
        .attr('class', 'x axis')
        .append('text')
        .attr('y', height + margin.top + 12)
        .attr('x', width / 2)
        .attr('font-size', 16)
        .text('Epoch')

      svg.append('clipPath')
        .attr('id', 'clip')
        .append('rect')
        .attr('width', width)
        .attr('height', height)

      var line = d3.line()
        .curve(d3.curveLinear)
        .x(function (d) { return x(d.x) })
        .y(function (d) { return y(d.y) })

      var linebase = svg.selectAll('.line')

      var line_color = ['#FF9933', '#0099FF', '#FF3300']

      linebase.data(this.accuracydata)
        .enter()
        .append('path')
        .attr('class', 'line')
        .attr('clip-path', 'url(#clip')
        .attr('stroke', function (d, i) {
            return line_color[i % 3]
        })
        .style('stroke-width', '2px')
        .attr('d', line)

      var points = svg.selectAll('.dots')
        .data(this.accuracydata)
        .enter()
        .append('g')
        .attr('class', 'dots')
        .attr('clip-path', 'url(#clip)')

      points.selectAll('.dot')
        .data(function (d, index) {
          var a = []
          d.forEach(function (point, i) {
            a.push({'index': index, 'point': point})
          })
          return a
        })
        .enter()
        .append('circle')
        .attr('class', 'dot')
        .attr('r', 2)
        .attr('fill', function (d, i) {
          return line_color[d.index % 3]
        })
        .attr('transform', function (d) {
          return 'translate(' + x(d.point.x) + ',' + y(d.point.y) + ')'
        })
    }
  }
</script>

<style>

    .grid .tick {
        stroke: lightgrey;
        opacity: 0.7;
        shape-rendering: crispEdges;
    }

    .grid path {
        stroke-width: 0;
    }

    .axis path {
        fill: none;
        stroke: #bbb;
        shape-rendering: crispEdges;
    }

    .axis text {
        fill: #555;
    }

    .axis line {
        stroke: #e7e7e7;
        shape-rendering: crispEdges;
    }

    .axis .axis-label {
        font-size: 14px;
    }

    .area {
        fill: none;
        stroke-width: 1px;
    }

    .line {
        fill: none;
        stroke-width: 1px;
    }

    .dot {

        stroke: transparent;
        stroke-width: 10px;
        cursor: pointer;
        /*pointer-events: all;*/
    }

    .dot:hover {
        stroke: rgba(68, 127, 255, 0.3);
    }

    .mouseout{
      width: 0;
      height: 0;
    }

    .d3-tip-accuracy{    /**/
			display: flex;
			flex-direction: column;
			position: absolute;   /**/
			line-height: 1;
			font-weight: bold;
      font-size:12px;
			padding:12px;
			background-color: antiquewhite;
			color: #fff;
			border-radius: 2px;
		}

		.d3-tip-accuracy:before{
			box-sizing: border-box;
			display: inline;
			font-size: 20px;
			width:100%;
			line-height: 1;
			color:  antiquewhite;
			content:'\25E3';
			position: absolute; 	/**/
			text-align: left;
			top: -10px;
			left: -1px;
		}

		.d3-tip-accuracy.n:before {
      margin: -1px 0 0 0;
      top: 100%;
      left: 0;
    }
</style>



